<template>
    <div class="container">
        <canvas id="text" width="600" height="200" style="border:0px solid #d3d3d3;"></canvas>
        <div id="wavin">
            <ul>
                <li><a href="https://www.wdcc.top">我的主页</a></li>
                <li><a href="https://github.com/Tangwego">我的Github</a></li>
                <li><a href="https://gitee.com/Tangwego">我的Gitee</a></li>
                <li><a href="https://blog.csdn.net/qq_35699215">我的CSDN</a></li>
                <li><a href="https://cv.wdcc.top/#/zh_cn">我的简历(中文)</a></li>
                <li><a href="https://cv.wdcc.top/#/en_us">我的简历(英文)</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:"Index",
    data(){
        return {
            msg: "Sorry! this page is not opened yet!, please visit: ",
            show: "Welcome to Wavin Don's Creative Center!"
        }
    },
    mounted(){
        let c = document.getElementById("text");
        let ctx = c.getContext("2d");

        ctx.font="30px Georgia";
        // 创建渐变
        var gradient = ctx.createLinearGradient(0,0,c.width,0);
        gradient.addColorStop("0","#f00");
        gradient.addColorStop("0.5","#00f");
        gradient.addColorStop("1.0","#151");
        // 用渐变填色
        ctx.fillStyle=gradient;
        ctx.fillText(this.show,10,50);
    },
    beforeCreate(){
        document.title = this.$route.meta.title
        if (!location.hostname) {
             return;
        }
        if (location.hostname.startsWith("github")) {
            window.location.href = "https://github.com/Tangwego";
        } else if (location.hostname.startsWith("gitee")) {
            window.location.href = "https://gitee.com/Tangwego";
        } else if (location.hostname.startsWith("csdn")) {
            window.location.href = "https://blog.csdn.net/qq_35699215";
        } else if (location.hostname.startsWith("cv")) {
            window.location.href = "https://cv.wdcc.top/#/zh_cn";
        } else {
            return;
        }
    }
}
</script>

<style scoped>
.container{
    width: auto;
    height: auto;
    background-color: #fff;
}

#text{
    padding-top: 50px;
}

#wavin{
    padding-top: 40px;
    background-color: #fff;
    height: 100%;
}

#wavin>ul{

}

#wavin>ul>li{
    display: inline-block;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 10px;
}

#wavin>ul>li>a{
}

#wavin>ul>li:hover,#wavin>ul>li>a:hover{
    background-color: red;
    color: white;
}

</style>


